@import "../../styles/helpers";

.head {
    max-width: 580px;
    margin: 0 auto 80px;
    text-align: center;
    @include d {
        margin-bottom: 64px; }
    @include m {
        text-align: left; } }

.stage {
    margin-bottom: 8px;
    color: $neutrals4; }

.list {
    display: flex;
    @include m {
        display: block; } }

.item {
    flex: 0 0 31%;
    padding: 34px 83px;
    @include x {
        padding: 34px 64px; }
    @include d {
        padding: 32px; }
    @include m {
        padding: 0; }
    &:first-child {
        padding-left: 0; }
    &:nth-child(2) {
        flex: 0 0 38%;
        border-width: 0 1px;
        border-style: solid;
        border-color: $neutrals6;
        @include m {
            border: none; }
        @include dark {
            border-color: $neutrals3; } }
    &:last-child {
        padding-right: 0; }
    &:not(:last-child) {
        @include m {
            margin-bottom: 24px;
            padding-bottom: 24px;
            border-bottom: 1px solid $neutrals6;
            @include dark {
                border-color: $neutrals3; } } } }

.icon {
    margin-bottom: 32px;
    svg {
        @include dark {
            fill: $neutrals8; } } }

.category {
    margin-bottom: 32px;
    font-weight: 600; }

.content {
    @include caption-1;
    color: $neutrals4; }
