// apply this to -> every element which has a class that starts with  "status"
[class^="status"] {
  display: inline-block;
  padding: 0 8px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: $neutrals8; }

.status-red {
  background: $red; }

.status-green {
  background: $green; }

.status-pink {
  background: $pink; }

.status-black {
  background: $neutrals2;
  @include dark-common {
    background: $neutrals8;
    color: $neutrals2; } }

.status-purple {
  background: $purple; }

.status-white {
  background: $neutrals8;
  color: $neutrals2; }

.status-stroke-red {
  box-shadow: inset 0 0 0 2px $red;
  color: $red; }

.status-stroke-green {
  box-shadow: inset 0 0 0 2px $green;
  color: $green; }

.status-stroke-pink {
  box-shadow: inset 0 0 0 2px $pink;
  color: $pink; }

.status-stroke-black {
  box-shadow: inset 0 0 0 2px $neutrals2;
  color: $neutrals2;
  @include dark-common {
    box-shadow: inset 0 0 0 2px $neutrals8;
    color: $neutrals8; } }

.status-stroke-purple {
  box-shadow: inset 0 0 0 2px $purple;
  color: $purple; }




